.editor-comment-layout-editor .editor-comment-container{display:flex;flex-direction:row;position:relative;align-items:center}.editor-comment-layout-editor .editor-comment-container .editor-comment-editor{margin-left:10px;padding:6px 15px;flex:1;outline:none;color:black;word-break:break-all;border-radius:20px;border:solid #e8e8e8 1px}.editor-comment-layout-editor .editor-comment-container .editor-comment-editor:empty:before{cursor:text;content:attr(placeholder);display:block;color:rgba(59,59,59,0.58)}.editor-comment-layout-editor .editor-comment-container .editor-comment-editor.editor-comment-border{border:solid rgba(141,141,141,0.78) 1px}.editor-comment-layout-editor .editor-comment-container .editor-comment-editor.editor-comment-uploading-comment{color:rgba(141,141,141,0.78)}.editor-comment-layout-editor .editor-comment-container .editor-comment-editor.editor-comment-disable{cursor:not-allowed}.editor-comment-layout-editor .editor-comment-container>.editor-comment-uploading{position:absolute;cursor:not-allowed;bottom:4px;right:10px}.editor-comment-layout-editor .editor-comment-container .editor-comment-signin-to-comment{margin-left:10px;display:table-cell;vertical-align:middle;cursor:pointer;color:#0087ea}.editor-comment-layout-editor .editor-comment-container .editor-comment-signin-to-comment:hover{text-decoration:underline}